    createUser: async function(userName,idCard,phoneNumber,email,password){
        const { web3 } = this;

        try{
            const networkId = await web3.eth.net.getId();

            const deployedNetworkOfToken = tokenArtifact.networks[networkId];
            const deployedNetworkOfUser = userArtifact.networks[networkId];
            const deployedNetworkOfpm = pmArtifact.networks[networkId];

            this.user = new web3.eth.Contract(
                userArtifact.abi,
                deployedNetworkOfUser.address
            );

            this.token = new web3.eth.Contract(
                tokenArtifact.abi,
                deployedNetworkOfToken.address,
            );

            this.pm = new web3.eth.Contract(
                pmArtifact.abi,
                deployedNetworkOfpm.address,
            );

            //donate
            this.transfercontract=deployedNetworkOfpm.address;

            const accounts = await web3.eth.getAccounts();
            this.account = accounts[0];
            // web3.eth.defaultAccount = this.account;

            return this.user.methods.createUser(accounts[0],userName,idCard,phoneNumber,email,password, deployedNetworkOfpm.address, deployedNetworkOfToken.address).send({
                from:this.account,
                gas:'1000000'
            });
        } catch(e){
            console.log(e);
        }
    },
